import React from 'react'
import FacebookImage from '../../../media/fb_image.jpg'

const NavbarHeaderForPhone = (props) => {
  const header={fontSize: '2em', marginTop: '0.5em'}
  const imageStyle = {
    width: '2.5em',
    height: '2.5em',
    borderRadius: '50%',
    marginTop: '1em',
    marginRight: '4em'
  }

  return (
    <div style={{margin: '0', display: 'flex', justifyContent: 'space-around', textAlign: 'center'}}>
      <img style={imageStyle} src={FacebookImage} alt='' />
      <h1 style={header}> {props.currentPage.toUpperCase()} </h1>
    </div>
  )
}

export default NavbarHeaderForPhone